import { IBaseMsg } from "../IMsg";


export interface IAppAction extends IBaseMsg {
    type: "studioUI.info.appAction",
    actionType: "open" | "close",
    time: Date;
    duration?: number
}

export interface IAppErrorMsg extends IBaseMsg {
    type: "studioUI.info.generalError",
    time: Date,
    duration: number

}

export interface IEditorMetricAction extends IBaseMsg {
    type: "studioUI.info.editorMetricAction",
    action: "load", // | other metric events
    // editorName from ISourceInfo["editorInfo"]
    duration: number,
    startTime: number,
}

export interface IChatbotStudioUI extends IBaseMsg {
    type: "studioUI.info.chatbot",
    aiResponse?: string;
    userQuestion?: string;
    chatHistory?: {
        id: number,
        content: string,
        role: "USER" | "AI",
        links?: Array<{ pageName: string, linkUrl: string }>,
        showFeedBack: boolean,
        followUpQuestions?: Array<string>,
        hubsterInfo?: {
            logId: string,
            correlationid: string
        }
    }[];
    liked?: boolean;
    disliked?: boolean
}

export interface ICreateUI extends IBaseMsg {
    type: "studioUI.info.createUI",
    msg: string,
    fileInfo: {
        name: string,
        size: number,
        type: string
    }
    modelName?: string,
    errorMsg?: any,
    generatedQjson?: string,
    success?: boolean
    durationMs?: string
}